.intro-section{
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    display: flex;
    flex-direction: row;

}
.intro-section__content{
    width: 50%;
    text-align: left;

}
.intro-section__image{
    align-self: center;
    width: 50%;
}
.intro-section__image>img{
    width: 80%;
}

.intro-section__content-title{
    font-size: 2.5rem;
    width: 80%;
}
.intro-section__content-text{
    width: 80%;
    font-size: 1.2rem;
    font-weight: 500;
    color: rgba(0, 0, 0, .5);
}
.intro-section__content-text>strong{
    
    font-size: 1.2rem;
    
    
}

@media only screen and (max-width: 768px){
    .intro-section{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
    }
    .intro-section__content{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    width: 100%;
    }
    .intro-section__content-title{
        text-align: center;
}
}
